}

/* 7.5. Floating Header
/* ---------------------------------------------------------- */
.floating-header {
  visibility: hidden;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  align-items: center;
  height: 60px;
  border-bottom: rgba(0, 0, 0, 0.06) 1px solid;
  background: rgba(0, 0, 0, 0.8);
  transition: all 500ms cubic-bezier(0.19, 1, 0.22, 1);
  transform: translate3d(0, -120%, 0);
  color: #fefefe;
}

.floating-active {
  visibility: visible;
  transition: all 500ms cubic-bezier(0.22, 1, 0.27, 1);
  transform: translate3d(0, 0, 0);
}

.floating-header-logo {
  overflow: hidden;
  margin: 0 0 0 20px;
  font-size: 1.6rem;
  line-height: 1em;
  letter-spacing: -1px;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.floating-header-logo a {
  display: flex;
  align-items: center;
  color: #333333;
  line-height: 1.1em;
  font-weight: 700;
  color: #fefefe;
}

.floating-header-logo a:hover {
  text-decoration: none;
}

.floating-header-logo img {
  max-height: 20px;
  margin: 0 10px 0 0;
}

.floating-header-divider {
  margin: 0 5px;
  line-height: 1em;
}

.floating-header-title {
  flex: 1;
  overflow: hidden;
  margin: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #fefefe;
}

.floating-header-share {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-left: 2%;
  font-size: 1.3rem;
  line-height: 1;
}

.floating-header-share a {
  display: flex;
  justify-content: center;
  align-items: center;
}

.floating-header-share svg {
  width: auto;
  height: 16px;
  fill: #fff;
}

.floating-header-share-label {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  margin-right: 10px;
  color: rgba(0, 0, 0, 0.7);
  font-weight: 500;
}

.floating-header-share-label svg {
  width: 18px;
  height: 18px;
  margin: 0 5px 0 10px;
  stroke: rgba(0, 0, 0, 0.7);
  transform: rotate(90deg);
}

.floating-header-share-tw,
.floating-header-share-fb {
  display: block;
  align-items: center;
  width: 60px;
  height: 60px;
  color: #fff;
  line-height: 48px;
  text-align: center;
  transition: all 500ms cubic-bezier(0.19, 1, 0.22, 1);
}

.floating-header-share-tw {
  background: #33b1ff;
}

.floating-header-share-fb {
  background: #005e99;
}

.progress {
  position: absolute;
  right: 0;
  bottom: -1px;
  left: 0;
  width: 100%;
  height: 2px;
  border: none;
  color: #ee3532;
  background: transparent;
  appearance: none;
}

.progress::-webkit-progress-bar {
  background-color: transparent;
}

.progress::-webkit-progress-value {
  background-color: #ee3532;
}

.progress::-moz-progress-bar {
  background-color: #ee3532;
}

.progress-container {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 2px;
  background-color: transparent;
}

.progress-bar {
  display: block;
  width: 50%;
  height: inherit;
  background-color: #ee3532;
}

@media (max-width: 900px) {
  .floating-header {
    height: 40px;
  }
  .floating-header-title,
  .floating-header-logo {
    font-size: 1.5rem;
  }
  .floating-header-share-tw,
  .floating-header-share-fb {
    width: 40px;
    height: 40px;
    line-height: 38px;
